import React from "react";
import "./ComplaintItem.css";
import LabelIcon from "../historylabel/LabelIcon";

const ComplaintSelectorDecrease = ({
  label,
  complaintDataDec,
  onChangeHandle,
}) => {
  return (
    <div className="complaint_selector_decrease">
      <div className="label_selector">{label}</div>

      {complaintDataDec.map((info) => (
        <LabelIcon
          key={info.id}
          label={info.label}
          id={info.id}
          onChangeHandle={onChangeHandle}
          value={info.value}
        />
      ))}
      {/* <LabelIcon label="المشي" />
      <LabelIcon label="الوقوف" />
      <LabelIcon label="النوم" />
      <LabelIcon label="الجلوس" />
      <LabelIcon label="مستمر" />
      <LabelIcon label="مسكنات" /> */}
    </div>
  );
};

export default ComplaintSelectorDecrease;
