import React from "react";
import { BrowserRouter as Router, Switch, Route } from "react-router-dom";

import Header from "./Header";
import MySessions from "./MySessions";
import MyInfo from "./MyInfo";

function App({ match }) {
  const handleSession = () => {
    console.log("session");
  };

  const handleFollow = () => {
    console.log("follow");
  };
  const id = match.params.id;
  return (
    <div>
      <Header
        handleClickSession={handleSession}
        handleClickFollow={handleFollow}
        id={id}
      />
      <Switch>
        <Route
          path={`/reception/patients/${id}/myinfo/:id`}
          exact
          component={MyInfo}
        />
        <Route
          path={`/reception/patients/${id}/mysession/:id`}
          exact
          component={MySessions}
        />
      </Switch>
    </div>
  );
}

export default App;
