import React from "react";
import "./Header.css";
import Logo from "../../assets/dr_ahmed_logo.png";
import { NavLink, Bars, NavMenu } from "./NavbarElements";
import { styled } from "@mui/material/styles";
import Button from "@mui/material/Button";
import { purple, blue } from "@mui/material/colors";

const SessionButton = styled(Button)(({ theme }) => ({
  color: theme.palette.getContrastText(purple[500] + "!important"),
  backgroundColor: purple[500] + "!important",
  fontFamily: "GESSTwoBoldBold !important",
  paddingBottom: "0.5rem !important",
  paddingTop: "0.5rem !important",
  paddingLeft: "1rem !important",
  paddingRight: "1rem !important",
  marginRight: "1rem !important",
  borderRadius: "5px !important",
  "&:hover": {
    backgroundColor: purple[700] + "!important",
  },
}));

const FollowButton = styled(Button)(({ theme }) => ({
  color: theme.palette.getContrastText(blue[500]),
  backgroundColor: blue[500] + "!important",
  fontFamily: "GESSTwoBoldBold !important",
  marginRight: "1rem !important",
  paddingBottom: "0.5rem !important",
  paddingTop: "0.5rem !important",
  paddingLeft: "1rem !important",
  paddingRight: "1rem !important",
  borderRadius: "5px !important",

  "&:hover": {
    backgroundColor: blue[700] + "!important",
  },
}));

//logo dimns 500 700
const Header = ({ handleClickSession, handleClickFollow, id }) => {
  return (
    <div className="LAYOUT_HEADER">
      <div className="">
        <img src={Logo} alt="React Logo" height="" width="" />
      </div>
      {/* <h5>حجز</h5>
      <h5>متابعة</h5>
      <h5>جلسات</h5>
      <h5>كشف جديد</h5> */}

      <div className="CENTER_BUTTONS">
        <FollowButton onClick={handleClickFollow}>متابعة</FollowButton>
        <SessionButton onClick={handleClickSession}>حجز جلسة</SessionButton>
      </div>

      <NavMenu>
        <NavLink to={`/reception/patients/${id}/mysession/${id}`} activeStyle>
          جلساتي
        </NavLink>
        <NavLink to={`/reception/patients/${id}/myinfo/${id}`} activeStyle>
          {/* <NavLink to={`/reception/patients/myinfo/${id}`} activeStyle> */}
          معلوماتي
        </NavLink>
      </NavMenu>
    </div>
  );
};

export default Header;
