import {
  collection,
  addDoc,
  updateDoc,
  doc,
  deleteDoc,
  query,
  where,
  getDocs,
  serverTimestamp,
  onSnapshot,
  setDoc,
  getDoc,
} from "firebase/firestore";
import db from "./firebase";

export const getAllPatientsId = async () => {
  const collectionRef = collection(db, "patients");
  const docSnap = await getDocs(collectionRef);
  return docSnap.docs;
  //   console.log(docSnap);
};

export const addNewPatient = async (data) => {
  const payload = { data, timestamp: serverTimestamp() };

  const collectionRef = collection(db, "patients");
  const docSnap = await getDocs(collectionRef);
  const id = docSnap.size + 100;

  console.log("Document data:", id);
  var idString = "" + id;

  await setDoc(doc(db, "patients", idString), payload);
  return id;
};

export const addNewComplaint = async (data) => {
  const payload = { data, timestamp: serverTimestamp() };

  const collectionRef = collection(db, "complaints");
  // Add a new document with a generated id.
  const docRef = await addDoc(collectionRef, payload);
  console.log("Document written with ID: ", docRef.id);
};

export const addNewExamination = async (data) => {
  //examination
  const collectionRef = collection(db, "examination");
  // Add a new document with a generated id.
  const docRef = await addDoc(collectionRef, data);
  console.log("Document written with ID: ", docRef.id);
};

export const getDocByID = async (id) => {
  return (await getDoc(doc(db, "patients", "" + id))).data();
};

export const getComplaintByID = async (id) => {
  return (await getDoc(doc(db, "complaints", "" + id))).data();
};
