export const getExaminationById = async (id) => {
  return (await getDoc(doc(db, "examination", "" + id))).data();
};

export const handleEdit = async (id) => {
  const name = prompt("Enter color name");
  const value = prompt("Enter color value");

  const docRef = doc(db, "patients", id);
  const payload = { name, value, timestamp: serverTimestamp() };

  updateDoc(docRef, payload);
};

export const handleDelete = async (id) => {
  const docRef = doc(db, "patients", id);
  await deleteDoc(docRef);
};

export const handleQueryDelete = async () => {
  const userInputName = prompt("Enter color name");

  const collectionRef = collection(db, "patients");
  const q = query(collectionRef, where("name", "==", userInputName));
  const snapshot = await getDocs(q);

  const results = snapshot.docs.map((doc) => ({ ...doc.data(), id: doc.id }));

  results.forEach(async (result) => {
    const docRef = doc(db, "colors", result.id);
    await deleteDoc(docRef);
  });
};

export const getComplaintsById = async (id) => {
  console.log("done", id);
  const collectionRef = collection(db, "complaints");
  const q = query(collectionRef, where(`data.patientId`, "==", id));
  return await getDocs(q);
};

export const getExaminationsById = async (id) => {
  const collectionRef = collection(db, "examination");
  const q = query(collectionRef, where("patientId", "==", id));
  return await getDocs(q);
};
