import * as React from "react";
import Box from "@mui/material/Box";
import InputLabel from "@mui/material/InputLabel";
import MenuItem from "@mui/material/MenuItem";
import FormControl from "@mui/material/FormControl";
import Select from "@mui/material/Select";
import "./BranchInput.css";

export default function BranchInput({
  onSelectBranch,
  branches,
  branchCurrent,
}) {
  const [branch, setBranch] = React.useState("");

  // const handleChange = (event) => {
  //   setBranch(event.target.value);
  // };

  return (
    <Box sx={{ minWidth: 120 }}>
      <FormControl fullWidth>
        {/* <InputLabel id="demo-simple-select-label">اسم الفرع</InputLabel> */}
        <Select
          labelId="branch-select-label"
          id="branch-select"
          value={branchCurrent}
          onChange={(event) => onSelectBranch(event)}
          displayEmpty
          inputProps={{ "aria-label": "Without label" }}
        >
          <MenuItem value="">
            <em>اسم الفرع</em>
          </MenuItem>
          <MenuItem value={branches[0].id}>فرع 1</MenuItem>
          <MenuItem value={branches[1].id}>فرع 2</MenuItem>
          <MenuItem value={branches[2].id}>فرع 3</MenuItem>
        </Select>
      </FormControl>
    </Box>
  );
}
