import React from "react";

import { Route, Switch } from "react-router-dom";
import Today from "./Today";
import Locations from "./Locations";
import Cashes from "./Cashes";
import Analytics from "./Analytics";
import Settings from "./Settings";
import AddPrescriptions from "../doctor/AddPrescription";

// import Dashboard from "../pages/Dashboard";
// import Customers from "../pages/Customers";

const Routes = () => {
  return (
    <Switch>
      <Route path="/admin/today" exact component={Today} />
      <Route path="/admin/locations" component={Locations} />
      <Route path="/admin/prescriptions" component={AddPrescriptions} />
      <Route path="/admin/cashes" component={Cashes} />
      <Route path="/admin/analytics" component={Analytics} />
      <Route path="/admin/settings" component={Settings} />
    </Switch>
  );
};

export default Routes;
