import React from "react";
import "./ComplaintItem.css";
import LabelIcon from "../historylabel/LabelIcon";

const ComplaintSelector = ({ label, complaintDataKind, onChangeHandle }) => {
  return (
    <div className="complaint_selector">
      <div className="label_selector">{label}</div>

      {complaintDataKind.map((info) => (
        <LabelIcon
          key={info.id}
          label={info.label}
          id={info.id}
          onChangeHandle={onChangeHandle}
          value={info.value}
        />
      ))}
      {/* <LabelIcon label="حرقان" />
      <LabelIcon label="تنميل" />
      <LabelIcon label="طعنات" />
      <LabelIcon label="شكشكة" />
      <LabelIcon label="تقل / انتشار" />
      <LabelIcon label="كهرباء" /> */}
    </div>
  );
};

export default ComplaintSelector;
