import React from "react";
import PatientInput from "../../components/patientInfoInput/PatientInput";
import ImageCard from "./ImageCard";
import MenuItem from "@mui/material/MenuItem";
import FormControl from "@mui/material/FormControl";
import Select from "@mui/material/Select";
import Button from "@mui/material/Button";
import { Link } from "react-router-dom";
import { useEffect, useState } from "react";
import { getComplaintsById } from "../../api/utils";

import "./style.css";
import { styled } from "@mui/material/styles";
import { purple, blue } from "@mui/material/colors";
import ComplaintItem from "../../components/complaintItem/ComplaintItem";
import Loader from "react-loader-spinner";
import { addNewExamination } from "../../api/utils";

const PrintButton = styled(Link)(({ theme }) => ({
  color: theme.palette.getContrastText(blue[100]),
  backgroundColor: blue[700] + "!important",
  fontFamily: "GESSTwoBoldBold !important",
  paddingBottom: "0.5rem !important",
  paddingTop: "0.5rem !important",
  paddingLeft: "2rem !important",
  paddingRight: "2rem !important",
  borderRadius: "10px !important",
  textTransform: "none !important",
  fontWeight: "bold",
  margin: "20px !important",
  textDecoration: "none !important",

  "&:hover": {
    backgroundColor: purple[700] + "!important",
  },
}));

const SaveButton = styled(Button)(({ theme }) => ({
  color: theme.palette.getContrastText(blue[100]),
  backgroundColor: blue[700] + "!important",
  fontFamily: "GESSTwoBoldBold !important",
  paddingBottom: "0.5rem !important",
  paddingTop: "0.5rem !important",
  paddingLeft: "2rem !important",
  paddingRight: "2rem !important",
  borderRadius: "10px !important",
  textTransform: "none !important",
  fontWeight: "bold",
  margin: "20px !important",
  textDecoration: "none !important",

  "&:hover": {
    backgroundColor: purple[700] + "!important",
  },
}));
function Examination({ match }) {
  const [loader, setLoader] = React.useState(false);

  const patientId = match.params.id;
  // console.log(match);
  const [currentLabel, setCurrentLabel] = useState("");
  const [ddInbut, setDDInput] = React.useState();
  const [ddNote, setNoteInput] = React.useState();

  // data for imageCard
  const [originalpainPlaces, setOriginalPainPlaces] = React.useState([
    {
      url: "(/assets/back.png)",
      title: "back left",
      value: false,
      position: "",
    },
    {
      url: "(/assets/back.png)",
      title: "back right",
      value: false,
      position: "",
    },
    {
      url: "(/assets/forword.png)",
      title: "forward left",
      value: false,
      position: "",
    },
    {
      url: "(/assets/forword.png)",
      title: "forward right",
      value: false,
      position: "",
    },
    {
      url: "(/assets/inner_left.png)",
      title: "inner left",
      value: false,
      position: "",
    },
    {
      url: "(/assets/inner_right.png)",
      title: "inner right",
      value: false,
      position: "",
    },
  ]);

  const getComplaintswithID = async () => {
    const docSnap = await getComplaintsById(match.params.id);
    const newLabel = docSnap.size + 1;
    console.log(newLabel);
    setCurrentLabel("P" + newLabel);
  };
  const addExamination = async () => {
    console.log("cliked");
    setLoader(true);
    console.log(patientId, ddInbut, ddNote, diagnosis, originalpainPlaces);
    await addNewExamination({
      patientId,
      ddInbut,
      ddNote,
      diagnosis,
      originalpainPlaces,
    });
    console.log("donnnnnne");
    setLoader(false);
  };

  useEffect(() => {
    // getIDs();
    getComplaintswithID();
  }, []);

  //data for prescription
  // const [prescription, setPrescription] = React.useState([
  //   {
  //     d_name: "lumbar disc",
  //     d_items: [
  //       { item_name: "Conventin 300 mg-Cap", item_name_ar: "حبة بعد العشاء" },
  //       { item_name: "Algasal - Cream", item_name_ar: "دهان صباحا و مساء" },
  //       { item_name: "Milga - Tab", item_name_ar: "حبة بعد الفطار و العشاء" },
  //       { item_name: "Dimra - Tab", item_name_ar: "حبة بعد الغداء" },
  //     ],
  //     d_instructions: [
  //       "يفضل تجنب انحناء الظهر اثناء الوقوف وعدم حمل الاشياء اثناء ثني الظهر",
  //       "يفضل عدم الجلوس لفترات طويلة في وضع واحد ويرجي تغيير وضعية الجلوس كل فترة",
  //       "يفضل تجنب حمل الاشياء الثقيلة في وضع خاطئ",
  //     ],
  //     d_protocol: [
  //       "ldi + b.v",
  //       "ld2 + mi",
  //       "ld3 + mi",
  //       "ld4 + b.v",
  //       "ld4 + b.c + b.v",
  //       "s.c",
  //     ],
  //   },
  // ]);

  const [diagnosis, setDiagnosis] = React.useState("");

  const handleChange = (event) => {
    setDiagnosis(event.target.value);
  };
  const handleDDInputChange = (event) => {
    console.log(event.target.value);
    setDDInput(event.target.value);
    console.log(ddInbut);
  };
  const handleNoteInputChange = (event) => {
    console.log(event.target.value);
    setNoteInput(event.target.value);
    console.log(ddNote);
  };

  const handleChangeImageCard = (event) => {
    console.log(event);

    if (event.value) {
      for (let x of originalpainPlaces) {
        if (x.title == event.title) {
          const index = originalpainPlaces.findIndex((c) => c == x);
          console.log(index);
          const painPlacess = [...originalpainPlaces];
          painPlacess[index].value = event.value;
          painPlacess[index].position = event.position;
          setOriginalPainPlaces(painPlacess);
          console.log(originalpainPlaces);
        }
      }
    }
  };
  return (
    <div className="examination_container">
      <Loader
        style={{ position: "absolute", top: "60%", left: "50%", zIndex: 10 }}
        type="Puff"
        color="#ff0000"
        // height={550}
        // width={80}
        visible={loader}
      />
      <ComplaintItem label={currentLabel} id={match.params.id} />
      <PatientInput
        label={"D.D."}
        id={""}
        onChangeHandle={handleDDInputChange}
        // value={ddInbut.value}
      />
      <PatientInput
        label={"Note"}
        id={""}
        onChangeHandle={handleNoteInputChange}
        // value={ddNote.value}
      />
      <ImageCard handleChangePainPlaces={handleChangeImageCard} />
      <FormControl sx={{ m: 1, minWidth: 300 }}>
        <Select
          value={diagnosis}
          onChange={handleChange}
          displayEmpty
          inputProps={{ "aria-label": "Without label" }}
        >
          <MenuItem value="">
            <em>D.D.</em>
          </MenuItem>
          <MenuItem value={"lumbar disc"}>lumbar disc</MenuItem>
          <MenuItem value={20}>Twenty</MenuItem>
          <MenuItem value={30}>Thirty</MenuItem>
        </Select>
      </FormControl>
      <div style={{ display: "flex" }}>
        <SaveButton onClick={addExamination} to="">
          Save
        </SaveButton>
        <PrintButton
          to={`/doctor/prescription?idIs=2331&diagnosis=${diagnosis}`}
          target="_blank"
        >
          P r i n t
        </PrintButton>
      </div>
    </div>
  );
}

export default Examination;
