import React from "react";
import { BrowserRouter as Router, Switch, Route } from "react-router-dom";
import Home from "./HomeRecp";
import NewPatient from "./NewPatient";
import FollowPatient from "./FollowPatient";
import SessionsPatient from "./SessionsPatient";
import BookPatients from "./BookPatients";
import Header from "./Header";
///reception/drahmedclinic/new
function App() {
  return (
    <div>
      <Header />
      <Switch>
        <Route
          path="/reception/drahmedclinic/new"
          exact
          component={NewPatient}
        />
        <Route
          path="/reception/drahmedclinic/myappointments"
          exact
          component={SessionsPatient}
        />
      </Switch>
    </div>
  );
}

export default App;
