const ImageButton = styled(FormControlLabel)((props) => ({
  ".MuiFormControlLabel-label": {
    backgroundImage: `url${props.image.url}`,
    backgroundSize: "contain",
    width: "104px",
    height: "187px",
    borderRadius: "0",
    backgroundRepeat: "no-repeat",
    alignContent: "center",
    backgroundPosition: "center",
    backgroundColor: "transparent",
    id: "asdf",
    padding: "0 !important",
    margin: "0 !important",
    position: "relative",
  },
}));

export default function ImageCard({ handleChangePainPlaces }) {
  const originalPainPlases = [
    {
      url: "(/assets/back.png)",
      title: "back left",
      value: false,
      position: "",
    },
    {
      url: "(/assets/back.png)",
      title: "back right",
      value: false,
      position: "",
    },
    {
      url: "(/assets/forword.png)",
      title: "forward left",
      value: false,
      position: "",
    },
    {
      url: "(/assets/forword.png)",
      title: "forward right",
      value: false,
      position: "",
    },
    {
      url: "(/assets/inner_left.png)",
      title: "inner left",
      value: false,
      position: "",
    },
    {
      url: "(/assets/inner_right.png)",
      title: "inner right",
      value: false,
      position: "",
    },
  ];
  const [painPlaces, setPainPlaces] = React.useState([
    {
      url: "(/assets/back.png)",
      title: "back left",
      value: false,
      position: "",
    },
    {
      url: "(/assets/back.png)",
      title: "back right",
      value: false,
      position: "",
    },
    {
      url: "(/assets/forword.png)",
      title: "forward left",
      value: false,
      position: "",
    },
    {
      url: "(/assets/forword.png)",
      title: "forward right",
      value: false,
      position: "",
    },
    {
      url: "(/assets/inner_left.png)",
      title: "inner left",
      value: false,
      position: "",
    },
    {
      url: "(/assets/inner_right.png)",
      title: "inner right",
      value: false,
      position: "",
    },
  ]);
  //set target div and mark dev diunmicy
  const setTargetMarkSetting = (targetDiv, divMark, targetTitle) => {
    // setPainPlaces(originalPainPlases);

    targetDiv.addEventListener("click", (event) => {
      //console.log(targetDiv);

      //reset painplaces
      //get item position
      var itemPosY = targetDiv.getBoundingClientRect().top;
      var itemPosX = targetDiv.getBoundingClientRect().left;
      console.log(itemPosY, itemPosX);

      // get mouse click coords
      var currentPosY = event.clientY - itemPosY;
      var currentPosX = event.clientX - itemPosX;
      console.log(currentPosY, currentPosX);
      // const painPlacess = [...originalPainPlases];

      for (let x of painPlaces) {
        if (x.title == targetTitle) {
          // console.log("target", x.title);
          x.position = `${currentPosY},${currentPosX}`;
          console.log("target", x.position);
          // handleChangePainPlaces(x);
          const index = painPlaces.findIndex((c) => c == x);
          console.log(index);
          const painPlacess = [...painPlaces];
          painPlacess[index].position = `${currentPosY},${currentPosX}`;
          // setPainPlaces(originalPainPlases);

          // setPainPlaces(painPlacess);
          // console.log(painPlaces);
        }
      }

      // set spot coords
      divMark.style.top = currentPosY + "px";
      divMark.style.left = currentPosX + "px";
      divMark.style.display = "block";
    });
  };

  useEffect(() => {
    //spot element add to span
    // const spotElement = `<div id="divMark"></div>`;
    var targetDiv = document
      .getElementById("image_card_id")
      .getElementsByClassName("MuiFormControlLabel-label");
    for (let i = 0; i < targetDiv.length; i++) {
      const spotElement = `<div id="divMark${i}"></div>`;
      targetDiv[i].innerHTML = spotElement;
    }

    var divMark0 = document.getElementById("divMark0");
    var divMark1 = document.getElementById("divMark1");
    var divMark2 = document.getElementById("divMark2");
    var divMark3 = document.getElementById("divMark3");
    var divMark4 = document.getElementById("divMark4");
    var divMark5 = document.getElementById("divMark5");

    setTargetMarkSetting(targetDiv[0], divMark0, "back left");
    setTargetMarkSetting(targetDiv[1], divMark1, "back right");
    setTargetMarkSetting(targetDiv[2], divMark2, "forward left");
    setTargetMarkSetting(targetDiv[3], divMark3, "forward right");
    setTargetMarkSetting(targetDiv[4], divMark4, "inner left");
    setTargetMarkSetting(targetDiv[5], divMark5, "inner right");
  }, []);
  return (
    <div className="image_card" id="image_card_id">
      {painPlaces.map((i) => (
        <div className="card_container">
          <ImageButton
            className="image_button"
            key={i.title}
            image={i}
            control={
              <Checkbox
                fontSize="large"
                onChange={(event) => {
                  console.log(event.target.name);
                  console.log(event.target.checked);
                  const index = painPlaces.findIndex(
                    (c) => c.title == event.target.name
                  );
                  console.log(index);
                  const painPlacess = [...painPlaces];
                  painPlacess[index].value = event.target.checked;
                  setPainPlaces(painPlacess);
                  handleChangePainPlaces(painPlacess[index]);
                }}
                icon={<RadioButtonUncheckedOutlinedIcon fontSize="medium" />}
                checkedIcon={<CheckCircleOutlinedIcon fontSize="medium" />}
                name={i.title}
                checked={i.value}
              />
            }
            // label={i.title}
            labelPlacement="bottom"
          />
          <span className="title_card">{i.title}</span>
        </div>
      ))}
    </div>
  );
}
