import React, { useEffect, useState } from "react";
import { Input } from "reactstrap";
import Button from "@mui/material/Button";
import { styled } from "@mui/material/styles";
import { purple, blue } from "@mui/material/colors";
import { addNewPrescription } from "../../api/utilsDatabase";
import Loader from "react-loader-spinner";

const SaveButton = styled(Button)(({ theme }) => ({
  color: theme.palette.getContrastText(blue[100]),
  backgroundColor: blue[700] + "!important",
  fontFamily: "GESSTwoBoldBold !important",
  paddingBottom: "0.5rem !important",
  paddingTop: "0.5rem !important",
  paddingLeft: "2rem !important",
  paddingRight: "2rem !important",
  borderRadius: "10px !important",
  textTransform: "none !important",
  fontWeight: "bold",
  margin: "20px !important",
  textDecoration: "none !important",
  alignSelf: "center",

  "&:hover": {
    backgroundColor: purple[700] + "!important",
  },
}));

function AddPrescription(props) {
  //   const [ddItems, setDDItems] = useState([]);
  //   const [ddInstructions, setDDInstructions] = useState([]);
  //   const [ddProtocol, setDDProtocol] = useState([]);
  const [loader, setLoader] = React.useState(false);

  const [ddValue, setDDValue] = useState("");
  const [d_items, setDItems] = useState([
    { name: "1", item_name: "", item_name_ar: "" },
    { name: "2", item_name: "", item_name_ar: "" },
    { name: "3", item_name: "", item_name_ar: "" },
    { name: "4", item_name: "", item_name_ar: "" },
    { name: "5", item_name: "", item_name_ar: "" },
    { name: "6", item_name: "", item_name_ar: "" },
  ]);
  const [d_instructions, setDInstructions] = useState([
    { name: "1", value: "" },
    { name: "2", value: "" },
    { name: "3", value: "" },
    { name: "4", value: "" },
    { name: "5", value: "" },
    { name: "6", value: "" },
  ]);
  const [d_protocol, setDProtocol] = useState([
    { name: "1", value: "" },
    { name: "2", value: "" },
    { name: "3", value: "" },
    { name: "4", value: "" },
    { name: "5", value: "" },
    { name: "6", value: "" },
  ]);
  const onSaveData = async () => {
    // console.log(ddValue, d_items, d_instructions, d_protocol);
    setLoader(true);
    console.log(
      await addNewPrescription(ddValue, d_items, d_instructions, d_protocol)
    );
    setLoader(false);
  };
  const handleChangeTherapy = (event) => {
    console.log(event.target.name, event.target.id);
    const index = d_items.findIndex((x) => x.name == event.target.name);
    console.log("index", index);
    const basicinfos = [...d_items];
    const contentIndex = basicinfos[index];
    basicinfos[index][event.target.id] = event.target.value;
    // contentIndex[event.target.id] = event.target.value;
    const newInfo = { ...d_items, basicinfos };
    setDItems(newInfo.basicinfos);
    console.log(d_items);
  };

  const handleInstructions = (event) => {
    console.log(event.target.name);
    const index = d_instructions.findIndex((x) => x.name == event.target.name);

    const d_instructionss = [...d_instructions];
    d_instructionss[index].value = event.target.value;
    const newInfo = { ...d_instructions, d_instructionss };

    setDInstructions(newInfo.d_instructionss);
    console.log(d_instructions);
  };

  const handleProtocol = (event) => {
    console.log(event.target.name);
    const index = d_protocol.findIndex((x) => x.name == event.target.name);

    const d_protocols = [...d_protocol];
    d_protocols[index].value = event.target.value;
    const newInfo = { ...d_protocol, d_protocols };

    setDProtocol(newInfo.d_protocols);
    console.log(d_protocol);
  };

  return (
    <div
      className=""
      style={{
        display: "flex",
        flexDirection: "column",
        position: "relative",
        backgroundColor: "#b3b3b3",
        borderRadius: "10px",
      }}
    >
      <Loader
        style={{ position: "absolute", top: "70%", left: "50%", zIndex: 10 }}
        type="Puff"
        color="#ff0000"
        // height={550}
        // width={80}
        visible={loader}
      />
      <Input
        onChange={(event) => {
          setDDValue(event.target.value);
        }}
        placeholder={"D.D."}
        style={{
          textAlign: "center",
          width: "50%",
          alignSelf: "center",
          marginTop: "1rem ",
        }}
      />

      {d_items.map((item) => (
        <div key={item.name} className="item_Therapy">
          <Input
            placeholder={item.item_name}
            style={{ textAlign: "left" }}
            onChange={handleChangeTherapy}
            name={item.name}
            id={"item_name"}
          />
          <Input
            style={{ fontFamily: "GESSTwoBoldBold" }}
            placeholder={item.item_name_ar}
            onChange={handleChangeTherapy}
            name={item.name}
            id={"item_name_ar"}
          />
        </div>
      ))}

      <span
        style={{
          fontFamily: "GESSTwoBoldBold",
          alignSelf: "center",
          backgroundColor: "rgb(1, 112, 1)",
          paddingRight: "5px",
          paddingLeft: "5px",
          color: "white",
          borderRadius: "5px",
          fontSize: "1.2rem",
        }}
      >
        تعليمات هامة من الطبيب
      </span>
      {d_instructions.map((item, index) => (
        <div
          key={index}
          className="inst_item"
          style={{
            // alignSelf: "flex-end",
            padding: "20px",
            fontFamily: "GESSTwoBoldBold",
          }}
        >
          <Input
            style={{ fontFamily: "GESSTwoBoldBold" }}
            placeholder={""}
            onChange={handleInstructions}
            name={item.name}
          />
        </div>
      ))}

      <span
        style={{
          fontFamily: "GESSTwoBoldBold",
          alignSelf: "center",
          backgroundColor: "#004bff",
          paddingRight: "5px",
          paddingLeft: "5px",
          color: "white",
          borderRadius: "5px",
          fontSize: "1.5rem",
        }}
      >
        Therapeutic protocol
      </span>

      {d_protocol.map((item, index) => (
        <div className="item_Therapy" key={index}>
          <Input placeholder={""} onChange={handleProtocol} name={item.name} />
        </div>
      ))}

      <SaveButton onClick={onSaveData}> S A V E </SaveButton>
    </div>
  );
}

export default AddPrescription;
