import React from "react";
import LabelIcon from "./LabelIcon";
import FormGroup from "@material-ui/core/FormGroup";
import "./LabelIcon.css";
import { Input } from "reactstrap";

const FullPatientHistory = ({ histories, onChangeHandle }) => {
  return (
    <div className="">
      <FormGroup row>
        {histories.map((history) => (
          <LabelIcon
            key={history.id}
            label={history.label}
            id={history.id}
            value={history.value}
            onChangeHandle={onChangeHandle}
          />
        ))}

        <Input className="input_row" placeholder="اخري   " />
        <Input className="input_row" placeholder="عمليات سابقة   " />
      </FormGroup>
    </div>
  );
};

export default FullPatientHistory;
