import { ref, set, child, get, onValue, onChildAdded } from "firebase/database";
import db from "./firebaseDatabase";

export const dbb = db;

export const addNewPrescription = async (
  ddValue,
  d_items,
  d_instructions,
  d_protocol
) => {
  console.log(ddValue, d_items, d_instructions, d_protocol);
  return await set(ref(db, "prescription/" + ddValue), {
    d_items,
    d_instructions,
    d_protocol,
  });
};

export const getPrescription = async (diagnosis) => {
  console.log("diagnosis", diagnosis);
  var data;
  const starCountRef = ref(db, "prescription/" + diagnosis);
  console.log("starCountRef", starCountRef);

  return await onValue(starCountRef, (snapshot) => {
    data = snapshot.val();
    console.log("data", data);
  });
};
