import React from "react";
import "./ComplaintItem.css";
import LabelIcon from "../historylabel/LabelIcon";

const ComplaintSelector = ({ label, onChangeHandle, complaintDataInc }) => {
  return (
    <div className="complaint_selector_increase">
      <div className="label_selector">{label}</div>

      {complaintDataInc.map((info) => (
        <LabelIcon
          key={info.id}
          label={info.label}
          id={info.id}
          value={info.value}
          onChangeHandle={onChangeHandle}
        />
      ))}
      {/* <LabelIcon label="المشي" />
      <LabelIcon label="الوقوف" />
      <LabelIcon label="النوم" />
      <LabelIcon label="الجلوس" />
      <LabelIcon label="مستمر" /> */}
    </div>
  );
};

export default ComplaintSelector;
