const LabelIcon = ({ label, id, onChangeHandle, value }) => {
  return (
    <div className="">
      <FormControlLabel
        control={
          <Checkbox
            fontSize="small"
            onChange={(event) => {
              onChangeHandle(event);
              // console.log(event.target.name);
            }}
            icon={<RadioButtonUncheckedOutlinedIcon fontSize="small" />}
            checkedIcon={<CheckCircleOutlinedIcon fontSize="small" />}
            name={id}
            checked={value}
          />
        }
        label={label}
      />
    </div>
  );
};

export default LabelIcon;
