import React from "react";
import { withStyles } from "@material-ui/core/styles";
import { blue, green, pink } from "@material-ui/core/colors";
import FormGroup from "@material-ui/core/FormGroup";
import FormControlLabel from "@material-ui/core/FormControlLabel";
import Checkbox from "@material-ui/core/Checkbox";
import CheckBoxOutlineBlankIcon from "@material-ui/icons/CheckBoxOutlineBlank";
import CheckBoxIcon from "@material-ui/icons/CheckBox";
import Favorite from "@material-ui/icons/Favorite";
import FavoriteBorder from "@material-ui/icons/FavoriteBorder";
import Radio from "@material-ui/core/Radio";
import CheckCircleOutlinedIcon from "@material-ui/icons/CheckCircleOutlined";
import RadioButtonUncheckedOutlinedIcon from "@material-ui/icons/RadioButtonUncheckedOutlined";
import RadioButtonCheckedOutlinedIcon from "@material-ui/icons/RadioButtonCheckedOutlined";
import "./LabelIcon.css";
import { InputGroup, InputGroupText, InputGroupAddon, Input } from "reactstrap";
import PatientInput from "../patientInfoInput/PatientInput";

function VitalSigns({ vitalSigns, onChangeHandle, readOnly }) {
  return (
    <div className="vitalsigns_info">
      {vitalSigns.map((info) => (
        <PatientInput
          key={info.id}
          label={info.label}
          id={info.id}
          value={readOnly && info.value}
          onChangeHandle={onChangeHandle}
        />
      ))}
      {/* <PatientInput label="الضغط" />
      <PatientInput label="النبض" />
      <PatientInput label="التنفس" />
      <PatientInput label="الحرارة" /> */}
    </div>
  );
}

export default VitalSigns;
