import React from "react";
import "./ComplaintItem.css";
import RadioGroup from "@mui/material/RadioGroup";
import FormControl from "@mui/material/FormControl";
import Radio from "@mui/material/Radio";
import FormLabel from "@mui/material/FormLabel";
import FormControlLabel from "@mui/material/FormControlLabel";
import { pink, blue, purple } from "@mui/material/colors";
import { styled } from "@mui/material/styles";
import Button from "@mui/material/Button";
import ComplaintSelector from "./ComplaintSelector";
import PatientInput from "../patientInfoInput/PatientInput";
import ComplaintSelectorIncrease from "./ComplaintSelectorIncrease";
import ComplaintSelectorDecrease from "./ComplaintSelectorDecrease";
import Fab from "@mui/material/Fab";
import AddIcon from "@mui/icons-material/Add";
import Loader from "react-loader-spinner";
import { addNewComplaint, getComplaintByID } from "../../api/utils";
import { useEffect, useState } from "react";
import { serverTimestamp } from "firebase/firestore";

