const StyledFab = styled(Fab)({
  borderRadius: "50% !important",
  color: "white !important",
  backgroundColor: blue[700] + "!important",
  alignSelf: "center",
  margin: "5px !important",
  "&:hover": {
    backgroundColor: purple[700] + "!important",
  },
});
const ComplaintItem = ({ label, id, complaintId, readOnly }) => {
  const [loader, setLoader] = React.useState(false);
  const [currentLabel, setCurrentLabel] = useState("");
  const timestamp = serverTimestamp();
  console.log(timestamp);

  const patientId = id;
  const [complaintValue, setComplaintValue] = React.useState("");
  const [complaintWhen, setComplaintWhen] = React.useState("");
  const [painDegree, setPainDegree] = React.useState("");
  const [infoComplain, setInfoComplain] = React.useState({
    // complaintDataInfo: [
    //   { id: 1, value: "" },
    //   { id: 2, value: "" },
    // ],
    complaintDataKind: [
      { id: 1, value: false, label: "حرقان" },
      { id: 2, value: false, label: "تنميل" },
      { id: 3, value: false, label: "طعنات" },
      { id: 4, value: false, label: "شكشكة" },
      { id: 5, value: false, label: "تقل / انتشار" },
      { id: 6, value: false, label: "كهرباء" },
    ],
    complaintDataDec: [
      { id: 1, value: false, label: "المشي" },
      { id: 2, value: false, label: "الوقوف" },
      { id: 3, value: false, label: "النوم" },
      { id: 4, value: false, label: "الجلوس" },
      { id: 5, value: false, label: "مستمر" },
      { id: 6, value: false, label: "مسكنات" },
    ],
    complaintDataInc: [
      { id: 1, value: true, label: "المشي" },
      { id: 2, value: false, label: "الوقوف" },
      { id: 3, value: false, label: "النوم" },
      { id: 4, value: false, label: "الجلوس" },
      { id: 5, value: false, label: "مستمر" },
    ],
  });

  const handleCompChange = (event) => {
    console.log(event.target.value);
    console.log(event);
    setComplaintValue(event.target.value);
  };

  const handlewhenChange = (event) => {
    console.log(event.target.value);
    console.log(event.target.name);
    setComplaintWhen(event.target.value);
  };

  const handleKindChange = (event) => {
    console.log(event.target.name);
    console.log(event.target.checked);
    const index = infoComplain.complaintDataKind.findIndex(
      (c) => c.id == event.target.name
    );
    console.log(index);
    const complaintDataKind = [...infoComplain.complaintDataKind];
    complaintDataKind[index].value = event.target.checked;
    const newInfo = { ...infoComplain, complaintDataKind };
    setInfoComplain(newInfo);
  };
  const handleIncChange = (event) => {
    console.log(event.target.name);
    console.log(event.target.checked);
    const index = infoComplain.complaintDataInc.findIndex(
      (c) => c.id == event.target.name
    );
    console.log(index);
    const complaintDataInc = [...infoComplain.complaintDataInc];
    complaintDataInc[index].value = event.target.checked;
    const newInfo = { ...infoComplain, complaintDataInc };
    setInfoComplain(newInfo);
  };
  const handleDecChange = (event) => {
    console.log(event.target.name);
    console.log(event.target.checked);
    const index = infoComplain.complaintDataDec.findIndex(
      (c) => c.id == event.target.name
    );
    console.log(index);
    const complaintDataDec = [...infoComplain.complaintDataDec];
    complaintDataDec[index].value = event.target.checked;
    const newInfo = { ...infoComplain, complaintDataDec };
    setInfoComplain(newInfo);
  };

  const handleRadioChange = (event) => {
    console.log(event.target.value);
    setPainDegree(event.target.value);
  };

  const onSaveData = async () => {
    console.log("cliked");
    console.log(
      label,
      patientId,
      complaintValue,
      complaintWhen,
      painDegree,
      infoComplain,
      timestamp
    );
    setLoader(true);

    await addNewComplaint({
      label,
      patientId,
      complaintValue,
      complaintWhen,
      painDegree,
      infoComplain,
    });
    console.log("donnnnnne");
    setLoader(false);
  };

  const getComplaintwithID = async () => {
    const doc = await getComplaintByID(complaintId);
    console.log(doc);
    setComplaintValue(doc.data.complaintValue);
    setComplaintWhen(doc.data.complaintWhen);
    setPainDegree(doc.data.painDegree);
    setInfoComplain(doc.data.infoComplain);
    setCurrentLabel(doc.data.label);

    // const hh = doc.history.histories;
    // setBranchCurrent(doc.branchCurrent);
    // setHistory(hh);
    // setInfo(doc.info);
  };

  useEffect(() => {
    // getIDs();
    if (readOnly) {
      getComplaintwithID();
    }
  }, []);

  const renderAddButton = () => {
    if (!readOnly) {
      return (
        <StyledFab
          size="medium"
          color="secondary"
          aria-label="add"
          onClick={onSaveData}
        >
          <AddIcon />
        </StyledFab>
      );
    } else {
      return null;
    }
  };
  return (
    <div className="complaint_container">
      <Loader
        style={{ position: "absolute", top: "40%", left: "50%", zIndex: 10 }}
        type="Puff"
        color="#ff0000"
        // height={550}
        // width={80}
        visible={loader}
      />
      <div className="first_clm">
        <div className="heigh_divider"></div>

        <FormControl component="fieldset">
          <RadioGroup
            className="raw_group"
            aria-label="gender"
            defaultValue="1"
            name="radio-buttons-group"
            onChange={handleRadioChange}
            value={painDegree}
          >
            <FormControlLabel
              id="label1"
              label="لا يوجد الم"
              //   sx={{ bgcolor: pink[800] }}
              control={
                <Radio
                  sx={{
                    color: pink[800],
                    "&.Mui-checked": {
                      color: pink[600],
                    },
                  }}
                />
              }
              labelPlacement="bottom"
              value="1"
            />
            <FormControlLabel
              id="label2"
              label="الم بسيط"
              control={
                <Radio
                  sx={{
                    color: pink[800],
                    "&.Mui-checked": {
                      color: pink[600],
                    },
                  }}
                />
              }
              labelPlacement="bottom"
              value="2"
            />
            <FormControlLabel
              id="label3"
              label="الم متوسط"
              control={
                <Radio
                  sx={{
                    color: pink[800],
                    "&.Mui-checked": {
                      color: pink[600],
                    },
                  }}
                />
              }
              labelPlacement="bottom"
              value="3"
            />
            <FormControlLabel
              id="label4"
              label="الم قوي"
              control={
                <Radio
                  sx={{
                    color: pink[800],
                    "&.Mui-checked": {
                      color: pink[600],
                    },
                  }}
                />
              }
              labelPlacement="bottom"
              value="4"
            />
            <FormControlLabel
              id="label5"
              label="الم قوي جدا"
              control={
                <Radio
                  sx={{
                    color: pink[800],
                    "&.Mui-checked": {
                      color: pink[600],
                    },
                  }}
                />
              }
              labelPlacement="bottom"
              value="5"
            />
            <FormControlLabel
              id="label6"
              label="اسوء الم ممكن"
              control={
                <Radio
                  sx={{
                    color: pink[800],
                    "&.Mui-checked": {
                      color: pink[600],
                    },
                  }}
                />
              }
              labelPlacement="bottom"
              value="6"
            />
          </RadioGroup>
        </FormControl>

        <div className="heigh_divider"></div>
        <ComplaintSelector
          label=": شكل الالم"
          complaintDataKind={infoComplain.complaintDataKind}
          onChangeHandle={handleKindChange}
        />
      </div>

      <div className="secend_clm">
        <div className="heigh_divider"></div>

        <PatientInput
          // {{readOnly} ? label={currentLabel} : label={label}}
          // label={if(readOnly) currentLabel}
          onChangeHandle={handleCompChange}
          value={complaintValue}
          label={readOnly ? currentLabel : label}
        />
        <PatientInput
          label="منذ متي ؟"
          onChangeHandle={handlewhenChange}
          value={complaintWhen}
        />

        <ComplaintSelectorIncrease
          label="يزيد مع ؟"
          complaintDataInc={infoComplain.complaintDataInc}
          onChangeHandle={handleIncChange}
        />
        <ComplaintSelectorDecrease
          label=" يخف ب  ؟"
          complaintDataDec={infoComplain.complaintDataDec}
          onChangeHandle={handleDecChange}
        />
      </div>

      {renderAddButton()}

      {/* {readOnly ? return (
        <StyledFab
          size="medium"
          color="secondary"
          aria-label="add"
          onClick={onSaveData}
        >
          <AddIcon />
        </StyledFab>
      ) : return null} */}

      {/* <StyledFab
        size="medium"
        color="secondary"
        aria-label="add"
        onClick={onSaveData}
      >
        <AddIcon />
      </StyledFab> */}
    </div>
  );
};

export default ComplaintItem;
