import * as React from "react";
import PatientInfo from "../../components/patientInfoInput/PatientInfo.js";
import FullPatientHistory from "../../components/historylabel/FullPatientHistory";
import VitalSigns from "../../components/historylabel/VitalSigns";
import BodyIndicators from "../../components/historylabel/BodyIndicators";
import Icontitle from "../../components/titlewithicon/Icontitle";
import SendIcon from "@mui/icons-material/Send";
import { styled } from "@mui/material/styles";
import Button from "@mui/material/Button";
import { purple, blue } from "@mui/material/colors";
import { addNewPatient } from "../../api/utils";
import Loader from "react-loader-spinner";
import { Alert } from "reactstrap";

const SaveButton = styled(Button)(({ theme }) => ({
  color: theme.palette.getContrastText(blue[100]),
  backgroundColor: blue[700] + "!important",
  fontFamily: "GESSTwoBoldBold !important",
  paddingBottom: "0.5rem !important",
  paddingTop: "0.5rem !important",
  paddingLeft: "2rem !important",
  paddingRight: "2rem !important",
  borderRadius: "10px !important",
  "&:hover": {
    backgroundColor: purple[700] + "!important",
  },
}));

const NewPatient = () => {
  const [patientId, setPatientsId] = React.useState("");
  const [loader, setLoader] = React.useState(false);
  const [alertLoader, setAlertLoader] = React.useState(false);
  const [branchCurrent, setBranchCurrent] = React.useState("");
  const [branches, setBranchs] = React.useState([
    { id: 1, value: "", label: "فرع 1" },
    { id: 2, value: "", label: "فرع 2" },
    { id: 3, value: "", label: "فرع 3" },
  ]);
  const [history, setHistory] = React.useState({
    histories: [
      { id: 1, value: false, label: "ضغط الدم" },
      { id: 2, value: false, label: "صعوبة بلع" },
      { id: 3, value: false, label: "غسيل كلوي" },
      { id: 4, value: false, label: "فقر الدم" },
      { id: 5, value: false, label: "سيولة الدم" },
      { id: 6, value: false, label: "الايدز" },
      { id: 7, value: false, label: "السرطان" },
      { id: 8, value: false, label: "الصرع" },
      { id: 9, value: false, label: "سكر الدم" },
      { id: 10, value: false, label: "الدرن" },
      { id: 11, value: false, label: "عرق ليلي" },
      { id: 12, value: false, label: "فقدان شهيه" },
      { id: 13, value: false, label: "HC فيروس" },
      { id: 14, value: false, label: "HB فيروس" },
      { id: 15, value: false, label: "جلطة" },
      { id: 16, value: false, label: "قيئ" },
      { id: 17, value: false, label: "منظم قلب" },
      { id: 18, value: false, label: "روماتويد" },
      { id: 19, value: false, label: "روماتيزم" },
      { id: 20, value: false, label: "فقدان وزن" },
      { id: 21, value: false, label: "شلل نصفي" },
      { id: 22, value: false, label: "شلل كلي" },
      { id: 23, value: false, label: "التهاب رئوي" },
      { id: 24, value: false, label: "C19كورونا" },
    ],
  });
  const [info, setInfo] = React.useState({
    basicinfos: [
      { id: 1, value: "", label: "العنوان" },
      { id: 2, value: "", label: "السن" },
      { id: 3, value: "", label: "الاسم" },
      { id: 4, value: "", label: "كيف تعرفت علينا؟" },
      { id: 5, value: "", label: "رقم الواتس" },
      { id: 6, value: "", label: "الوظيفة" },
    ],
    vitalSigns: [
      { id: 1, value: "", label: "الضغط" },
      { id: 2, value: "", label: "النبض" },
      { id: 3, value: "", label: "التنفس" },
      { id: 4, value: "", label: "الحرارة" },
    ],
    bodyIndicators: [
      { id: 1, value: "", label: "المطلوب يخسة" },
      { id: 2, value: "", label: "الوزن المثالي" },
      { id: 3, value: "", label: "مؤشر كتلة الجسم" },
      { id: 4, value: "", label: "الوزن" },
      { id: 5, value: "", label: "الطول" },
    ],
    // branches: [
    //   { id: 1, value: "", label: "فرع 1" },
    //   { id: 2, value: "", label: "فرع 2" },
    //   { id: 3, value: "", label: "فرع 3" },
    // ],
  });
  const handleChange = (event) => {
    console.log(event.target.name);
    console.log(event.target.checked);
    const index = history.histories.findIndex((c) => c.id == event.target.name);
    console.log(index);
    const histories = [...history.histories];
    histories[index].value = event.target.checked;
    setHistory({ histories });
    // console.log(history.histories);
  };
  const handleVitalChange = (event) => {
    console.log(event.target.value);
    console.log(event.target.name);
    const index = info.vitalSigns.findIndex((c) => c.id == event.target.name);
    console.log("index", index);
    const vitalSigns = [...info.vitalSigns];
    vitalSigns[index].value = event.target.value;
    const newInfo = { ...info, vitalSigns };
    setInfo(newInfo);
  };
  const handleBodyChange = (event) => {
    console.log(event.target.value);
    console.log(event.target.name);
    const index = info.bodyIndicators.findIndex(
      (c) => c.id == event.target.name
    );
    console.log("index", index);
    const bodyIndicators = [...info.bodyIndicators];
    bodyIndicators[index].value = event.target.value;
    console.log(bodyIndicators);
    const newInfo = { ...info, bodyIndicators };
    setInfo(newInfo);
    console.log(info);
  };
  const handleBasicChange = (event) => {
    console.log(event.target.value);
    console.log(event.target.name);
    const index = info.basicinfos.findIndex((c) => c.id == event.target.name);
    console.log("index", index);
    const basicinfos = [...info.basicinfos];
    basicinfos[index].value = event.target.value;
    const newInfo = { ...info, basicinfos };
    setInfo(newInfo);
  };

  const onSaveData = async () => {
    setLoader(true);

    const patientID = await addNewPatient({ history, info, branchCurrent });
    console.log("patientID", patientID);
    setPatientsId(patientID);

    console.log("donnnnnne");
    setLoader(false);
    setAlertLoader(true);
  };

  const handleSelectBranch = (event) => {
    console.log(event.target.value);
    setBranchCurrent(event.target.value);
  };

  return (
    <div style={{ position: "relative" }}>
      <Loader
        style={{ position: "absolute", top: "50%", left: "50%" }}
        type="Puff"
        color="#ff0000"
        // height={550}
        // width={80}
        visible={loader}
      />

      <Icontitle label="بيانات المريض" />
      <PatientInfo
        basicinfos={info.basicinfos}
        onChangeHandle={handleBasicChange}
        onSelectBranch={handleSelectBranch}
        branches={branches}
        branchCurrent={branchCurrent}
      />
      <Icontitle label="التاريخ المرضي" />
      <FullPatientHistory
        histories={history.histories}
        onChangeHandle={handleChange}
      />
      <Icontitle label="العلامات الحيوية" />
      <VitalSigns
        vitalSigns={info.vitalSigns}
        onChangeHandle={handleVitalChange}
      />
      <Icontitle label="مؤشرات الجسم" />
      <BodyIndicators
        bodyIndicators={info.bodyIndicators}
        onChangeHandle={handleBodyChange}
      />
      <div
        style={{
          display: "flex",
          alignItems: "center",
          justifyContent: "center",
          margin: "40px",
          flexDirection: "column",
        }}
      >
        <SaveButton onClick={onSaveData}>ارسال</SaveButton>
        <Alert
          color="primary"
          dismissible
          style={{ marginTop: "10px" }}
          isOpen={alertLoader}
        >
          ID is {patientId}
        </Alert>
      </div>
    </div>
  );
};

export default NewPatient;
