import "./patienInput.css";
import PatientInput from "./PatientInput";
import BranchInput from "../branchInput/BranchInput";

function PatientInfo({
  readOnly,
  basicinfos,
  onChangeHandle,
  onSelectBranch,
  branches,
  branchCurrent,
}) {
  return (
    <div className="patient_info">
      {basicinfos.map((info) => (
        <PatientInput
          key={info.id}
          label={info.label}
          id={info.id}
          value={readOnly && info.value}
          //
          onChangeHandle={onChangeHandle}
        />
      ))}
      {/* 
      <PatientInput label="العنوان" />
      <PatientInput label="السن" />
      <PatientInput label="الاسم" />
      <PatientInput label="كيف تعرفت علينا؟" />
      <PatientInput label="رقم الواتس" />
      <PatientInput label="الوظيفة" /> */}
      <BranchInput
        onSelectBranch={onSelectBranch}
        branches={branches}
        branchCurrent={branchCurrent}
      />
    </div>
  );
}

export default PatientInfo;
